.todo_main {
  text-align: center;
  width: 520px;
  margin: auto;
  background-color: #bcf4f5;
  border: 1px solid #00bdb8; }
  .todo_main h1 {
    color: #1f7292; }
  .todo_main .todo_div {
    background-color: #ffffff;
    border-bottom: 1px solid #6fe2d5;
    padding-bottom: 10px; }

.todo_header {
  text-align: center;
  border-bottom: 1px solid #6fe2d5;
  padding-bottom: 10px; }
  .todo_header .add_span {
    margin-left: 10px; }
    .todo_header .add_span img {
      vertical-align: middle;
      width: 21px;
      height: 21px; }
  .todo_header .inp {
    position: relative;
    margin: auto;
    width: 80%;
    max-width: 280px; }
    .todo_header .inp .label {
      position: absolute;
      top: 0;
      left: 0;
      font-size: 16px;
      color: #9098a9;
      font-weight: 500;
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
      transition: all 0.2s ease; }
    .todo_header .inp .border {
      position: absolute;
      bottom: -6px;
      left: 0;
      height: 2px;
      width: 100%;
      background: #07f;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
      transition: all 0.15s ease; }
    .todo_header .inp input {
      -webkit-appearance: none;
      width: 80%;
      border: 0;
      font-family: inherit;
      height: 30px;
      font-size: 16px;
      font-weight: 500;
      border-bottom: 2px solid #c8ccd4;
      background: none;
      border-radius: 0;
      color: #223254;
      transition: all 0.15s ease; }
    .todo_header .inp input:hover {
      background: rgba(34, 50, 84, 0.03); }
    .todo_header .inp input:not(:placeholder-shown) + span {
      color: #5a667f;
      -webkit-transform: translateY(-26px) scale(0.75);
              transform: translateY(-26px) scale(0.75); }
    .todo_header .inp input:focus {
      background: none;
      outline: none; }
      .todo_header .inp input:focus + span {
        color: #07f;
        -webkit-transform: translateY(-25px) scale(0.75);
                transform: translateY(-25px) scale(0.75); }
        .todo_header .inp input:focus + span + .border {
          -webkit-transform: scaleX(1);
                  transform: scaleX(1); }

.todo_section {
  display: inline-block;
  width: 86%;
  margin: auto;
  padding-left: 0px; }
  .todo_section .todo_complete .todo_content {
    color: #c7c2c2b2;
    text-decoration: line-through; }
  .todo_section .toggleCheck img {
    width: 20px;
    vertical-align: middle;
    margin-bottom: 1px; }
  .todo_section .todoItem {
    cursor: pointer;
    border-bottom: 1px solid #d9d9d9;
    padding: 10px 10px 0px 10px;
    display: inline-block;
    width: 100%;
    text-align: left; }
    .todo_section .todoItem:hover .todo_delete {
      visibility: visible; }
    .todo_section .todoItem .todo_content {
      width: 80%;
      border: none;
      display: inline-block;
      height: 24px;
      font-size: 11pt;
      background: none; }
    .todo_section .todoItem .todo_delete {
      visibility: hidden;
      margin-right: 10px;
      float: right; }
      .todo_section .todoItem .todo_delete img {
        width: 17px;
        height: 17px;
        margin-top: 5px; }

.todo_footer {
  text-align: center;
  margin-top: 10px;
  padding-bottom: 10px; }
  .todo_footer .mode {
    margin-right: 20px;
    cursor: pointer;
    padding: 3px 7px;
    border: 1px solid transparent; }
    .todo_footer .mode:hover {
      border: 1px solid #6491f7;
      border-radius: 11px; }
  .todo_footer .focus {
    border: 1px solid #6491f7;
    border-radius: 11px; }
  .todo_footer .todoLeft {
    float: left;
    margin-left: 10px;
    font-size: 10pt;
    margin-top: 3px; }

html,
body {
  height: 100%; }

body {
  display: grid;
  font-family: Avenir;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

